import React from 'react';
import styles from './charts.module.scss';
import "./other.scss";
import Week, { getIcon, getSongData } from './week';
import {
  BrowserRouter,
  HashRouter,
  Link,
  Navigate,
  Route,
  Routes,
  useParams
} from "react-router-dom";
import { Chart } from './chart';
import { FluentProvider, Portal, Skeleton, SkeletonItem, Tab, TabList, Theme, webDarkTheme, webLightTheme } from '@fluentui/react-components';
import { createPortal } from 'react-dom';

export interface IChart {
  name: string; file: string; data: IChartFile, urlseg: string; rating: string; startDay: number; 
}

export interface ISong {
  name: string; artist: string; img: string; album?: string | undefined; spotify?: string | undefined;
}

export interface ISongData extends ISong {
  pos: number; isNew: boolean; lastWeek: number | undefined; weeksAtOne: number; weeksInChart: number; max: number;
}

export interface IWeek {
  week: number; date: string; songs: string[]; video: string | undefined;actualDate:Date|undefined;
}

export interface IChartFile {
  songs: ISong[], weeks: IWeek[]
}

function App() {

  //const [chart, setChart] = React.useState<string | undefined>(undefined);
  const [charts, setCharts] = React.useState<IChart[] | undefined>(undefined);
  const [loading, setLoading] = React.useState<boolean>(true);
  const [currentSeg, setCurrentSeg] = React.useState<string>("home");

  const chartData = [];

  const fetchCharts = () => {
    fetch("./charts.json").then((d) => { return (d.json()); }).then(j => {
      setCharts(j);
    });
  }

  React.useEffect(() => {
    if (loading === true && charts && charts.filter(cha => cha.data===null).length>0) {
      const _file = charts.filter(cha => cha.data===null)[0].file;      
      fetch("./" + _file).then((d) => { return (d.json()); }).then(j => {
        var _ = require("lodash");
        let _charts:IChart[] = _.cloneDeep(charts);       
        _charts.filter(ch => ch.file===_file)[0].data = _.cloneDeep(j);
        _charts.filter(ch => ch.file===_file)[0].data.weeks.forEach(w => {
          w.actualDate = new Date(w.date.replace("Week of ",""));
        });
        setCharts(_charts);
        if(_charts.filter(l => l.data===null).length===0)
        {
          setLoading(false);
        }
      });
    }
  }, [charts]);

  React.useEffect(() => {
    fetchCharts();
  }, []);

  console.log(currentSeg)

  return <><section id={"main-navigation"} className={styles.navigation}><Navigation key={currentSeg} charts={charts} highlight={currentSeg}></Navigation></section>
  <HashRouter>
      <Routes>
      <Route path="/chart/:seg/week/:week/song/:song" element={<Chart setSeg={(s:string) => {setCurrentSeg(s);}} charts={charts} />} />
      <Route path="/chart/:seg/week/:week" element={<Chart setSeg={(s:string) => {setCurrentSeg(s);}} charts={charts} />} />
      <Route path="/chart/:seg" element={<Chart setSeg={(s:string) => { setCurrentSeg(s); }} charts={charts} />} />
      <Route path="*" element={<Home setSeg={(s:string) => { setCurrentSeg(s); }} key="home" charts={charts} />} />
    </Routes></HashRouter></>

}

export default App;

export function customLightTheme(): Theme {
  return {
    ...webLightTheme,
    fontFamilyBase: "neue-haas-grotesk-display"
  }
}

export function customDarkTheme(): Theme {
  return {
    ...webDarkTheme,
    fontFamilyBase: "neue-haas-grotesk-display"
  }
}


interface IHomeProps{
  charts:IChart[]|undefined,setSeg: (a:string) => void;
}

function Home(props: IHomeProps) {

  const { charts, setSeg } = props;

  React.useEffect(() => {
    setSeg("home");
    window.document.title = "The Personal Charts"
  }, []);


  if (charts) {
    if (charts.length === 1) {
      return <Navigate to={"/chart/" + charts[0].urlseg} />
    }
    else {
      return <>
        <FluentProvider theme={customLightTheme()}><div className={styles.home}>
        <div className={styles.homeIntro}>
          <div>        <h2>What are "The Personal Charts"?</h2>
        <p>"The Personal Charts" is a platform created so that everybody can share their taste in music with the public in a chart-like format.</p>
        <p>It originally only included "Yellow Charts", which is what the system was made for, but we decided to expand the site and allow others to share their preferences alongside mine.</p>
        </div>
        <div>
        <h2>Can I have my personal chart featured?</h2>
        <p>Yes, you can!</p>
        <p>If you already have a chart, or seeing this is what made you want to start one of your own, you are perfectly eligible for a spot on my website!</p>
        <p>Contact me on Discord, my username is theyellowsmithereen, and we'll discuss the next steps.</p>
        </div>
        </div>
                <div className={styles.homeCharts}>
        {charts.map((c: IChart) => {
          if (c.data!==null) {
            const week = c.data.weeks[c.data.weeks.length - 1];
            return <div key={c.urlseg}>
              <h2>{c.name} - {c.rating} {c.data.weeks[c.data.weeks.length-1].songs.length}</h2>
              <CompactChart weeks={c.data.weeks} week={week} songs={c.data.songs} />
              <Link to={"/chart/" + c.urlseg}><h3>View Chart <span>&gt;</span></h3></Link>
            </div>
          }
          else {
            return <div key={c.urlseg}>
              <h2>{c.name}</h2>
              <CompactChart weeks={undefined} week={undefined} songs={undefined} />
            </div>
          }
        })}
      </div></div></FluentProvider></>
    }
  }
  else {
    return <FluentProvider theme={customLightTheme()}><Skeleton aria-label="Loading Content">
      <SkeletonItem></SkeletonItem>
    </Skeleton></FluentProvider>
  }
}

interface ICompactChartProps {
  week: IWeek|undefined; songs: ISong[]|undefined;
  weeks: IWeek[]|undefined;
}

export function CompactChart(props: ICompactChartProps) {
  
  const { week, songs, weeks } = props;

  if(!week || !songs || !weeks)
  {
    const arr:number[] = [1,2,3,4,5];
    return <div className={styles.compactTable}>
        {arr.map((a) => <div key={"loading"+(a)} className={styles.compactRow + " " + styles.loadingCompactRow}>
        <Skeleton aria-label="Loading Content">
      <SkeletonItem size={48}></SkeletonItem>
    </Skeleton>         
        </div>)}
    </div>
  }
  else
  {

    const _data = getSongData(week.week,weeks,songs)
    return <div className={styles.compactTable}>{_data.slice(0,5).map((song:ISongData, index: number) => {
      const _icon = getIcon(song);
      return <div className={styles.compactRow} key={index}>
        <div>{index+1}</div>
        <div>{_icon}</div>
        <div><h3>{song.name}</h3>
        <p>{song.artist}</p></div>
        <div><img width="100" height="100" src={song.img}/></div>
      </div>
    })}</div>
  }
}

interface INavigationProps { 
  charts:IChart[]|undefined; highlight: string;
}

export function Navigation(props:INavigationProps)
{
    const { seg, week } = useParams();
    const { charts, highlight } = props;
    const [ navRedirect, setNavRedirect] = React.useState<undefined|string>(undefined);

    React.useEffect(() => {
      if(navRedirect) { 
        window.location.hash= "#" + navRedirect;
        setNavRedirect(undefined);
       }
    }, [navRedirect]);   

    return <FluentProvider className={styles.fluentNavigation} theme={customDarkTheme()}><section className={styles.navigation}>      
      <TabList className={styles.navigationTabList} defaultSelectedValue={highlight}>
        <Tab onClick={(event) => { setNavRedirect("/"); }} value={"home"}>Home</Tab>
        {
          (charts) && charts.map(c => <Tab key={c.urlseg} onClick={(event) => { setNavRedirect("/chart/" + c.urlseg); }} value={c.urlseg}>{c.name}</Tab>)
        }

      </TabList>
    </section></FluentProvider>

}

export function getSong(songs: ISong[], hash: string): ISong {
  let  song = songs.filter(s => (s.artist && s.name && s.artist.toLowerCase() + " - " + s.name.toLowerCase() === hash.toLowerCase()))[0];
  if(song)
  {
    return song;
  }
  else{
    return { name: "Not found", artist: "not found", img: "" }
  }
}