import React from 'react';
import styles from './charts.module.scss';
import { customLightTheme, getSong, ISong, ISongData, IWeek } from './App';
import { Link, Navigate, useParams } from 'react-router-dom';
import { Panel } from './panel';
import { Button, FluentProvider, makeStyles, Popover, PopoverSurface, PopoverTrigger, shorthands } from '@fluentui/react-components';
import { Calendar, DateRangeType } from "@fluentui/react-calendar-compat";
import {  CalendarRegular  } from "@fluentui/react-icons";

interface IWeekProps {
    week: number; weeks: IWeek[]; songs: ISong[], seg: string; name: string; rating: string; startDay: number
}

interface ICalendarProps {
    selectedDate: Date; startDay: number;
    selectDate: (date:Date) => void; minDate: Date; maxDate: Date;
}

function ShowCalendar(props: ICalendarProps) {

    const { selectedDate, selectDate,startDay, minDate, maxDate } = props;
    const _months = ["January","February","March","April","May","June","July","August","September","October","November","December"] 

    return <FluentProvider theme={customLightTheme()}><Calendar
    dateRangeType={DateRangeType.Week}
    highlightSelectedMonth minDate={minDate} maxDate={maxDate}
    showGoToToday
    onSelectDate={(date: Date, selectedDateRangeArray?: Date[]) => { if(selectedDateRangeArray)
    {
        selectDate(selectedDateRangeArray[0]);
    }
    }}
    value={selectedDate}
    firstDayOfWeek={startDay}
  /></FluentProvider>

}
const useClasses = makeStyles({
    container: {
      display: "flex",
      ...shorthands.gap("5px"),
    },
  
    icon24: { fontSize: "24px" },
    icon32: { fontSize: "32px" },
    icon48: { fontSize: "48px" },
  });

function Week(props: IWeekProps) {

    console.log("week render")
    const classes = useClasses();

    const { song } = useParams();

    const [highlightSong, setHighlightSong] = React.useState<undefined | number>(song ? parseInt(song) : undefined);

    const { week, weeks, songs, seg, name, rating, startDay } = props;

    const [activeRow, setActiveRow] = React.useState<number | undefined>(undefined);
    const [navigateTo, setNavigateTo] = React.useState<string | undefined>(undefined);

    const _week = weeks.filter(w => w.week === week)[0];

    const _awardIcon = <svg xmlns="http://www.w3.org/2000/svg" style={{ width: "100%", height: "auto" }} viewBox="0 0 18.072 17.316"><path data-name="Path 3055" d="M9.072 0L6.984 6.7H0l5.58 4.14-2.052 6.44 5.508-3.96 5.508 4-2.124-6.448L18.072 6.7h-6.84z" fill="currentColor"></path></svg>;
    const _topEntry = <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 37 37" style={{ width: "100%", height: "auto" }}><g data-name="Group 7175"><g data-name="Group 7173"><path data-name="Path 3043" d="M18.072 8.646l-2.088 6.7H9l5.58 4.14-2.052 6.44 5.508-3.96 5.508 4-2.124-6.448 5.652-4.172h-6.84z" fill="currentColor"></path></g><g data-name="Ellipse 542" fill="none" stroke="currentColor"><circle cx="18.5" cy="18.5" r="18.5" stroke="none"></circle><circle cx="18.5" cy="18.5" r="18"></circle></g></g></svg>;

    React.useEffect(() => {
        if (highlightSong !== undefined) {
            setTimeout(() => { setHighlightSong(undefined); }, 1500);
            const path = "div." + styles.row + ":nth-child(" + (highlightSong + 2) + ")";
            (document.querySelector(path) as Element).scrollIntoView({ block: "center", behavior: "smooth" });

        }
    }, [highlightSong]);

    if(navigateTo!==undefined)
    {
        return <Navigate to={"/chart/" + seg + "/week/" + (navigateTo)}/>
    }

    if (_week) {


        let _i: number = 1; let _highest: number | undefined = undefined;

        const _data = getSongData(week, weeks, songs);
        const _top = _data[0];

        const _nextWeek = weeks.filter(w => w.week > week)[0];
        const _prevWeek = weeks.filter(w => w.week < week).sort((a, b) => b.week - a.week)[0];
        const _minDate = weeks[0].actualDate as Date; const _maxDate = weeks[weeks.length-1].actualDate as Date;

        return <><div className={styles.header}>
            <h1>{name} - {rating} {_week.songs.length}</h1>
            <div className={styles.chartNavigation}>
                {_prevWeek ? <Link className="arrow-left" to={"/chart/" + seg + "/week/" + (_prevWeek.week)}>Previous week</Link> : <a/>}
                {weeks.length>1 && <FluentProvider theme={customLightTheme()}><Popover withArrow positioning={"below"}>
                    <PopoverTrigger disableButtonEnhancement>
                        <div className={styles.circleIcon}><CalendarRegular className={classes.icon32 } /></div>
                    </PopoverTrigger>
                    <PopoverSurface tabIndex={-1}>
                    <ShowCalendar minDate={_minDate} maxDate={_maxDate} startDay={startDay} selectDate={(d:Date) => {                        
                        let _newWeek = weeks.filter(w => w.actualDate && w.actualDate.toUTCString()===d.toUTCString())[0];
                        if(_newWeek && _newWeek.week !== week)
                        {                        
                            setNavigateTo(_newWeek.week.toString());
                        }
                    }} selectedDate={_week.actualDate as Date} />
                    </PopoverSurface>
                </Popover></FluentProvider> }
                {_nextWeek ? <Link className="arrow-right" to={"/chart/" + seg + "/week/" + (_nextWeek.week)}>Next week</Link> : <div></div>}
            </div>
            <div className={styles.columns + (_week.video ? "" : " " + styles.noVideo)}>
                <div>
                    <div className={styles.summary}>
                        <div>1</div><div><h3>{_top.name}</h3><span>{_top.artist}</span></div><div><img width="100" height="100" src={_top.img} /></div>
                    </div>
                    {_top.isNew ? <div className={styles.newThisWeek}>New this week!</div> : <div className={styles.moreData}><div>
                        <p>last week</p><h3>{_top.lastWeek}</h3></div><div><p>weeks at no.1</p><h3>{_top.weeksAtOne}</h3></div>
                        <div><p>weeks on chart</p><h3>{_top.weeksInChart}</h3></div></div>}
                </div>
                {_week.video &&
                    <div>
                        <h2>{name} - {rating} {_week.songs.length} Countdown Video</h2>
                    </div>}
            </div>
        </div>
            <div className={styles.table} id={seg}>
                <h2>{name} - {rating} {_week.songs.length}<span>{_week.date}</span></h2>
                <div className={styles.headerRow}>
                    <div className={styles.thin}>This week</div>
                    <div className={styles.thisWeek}>&nbsp;</div>
                    <div className={styles.thinnest}>Award</div>
                    <div className={styles.thinnest}>Last<br />Week</div>
                    <div className={styles.thinnest}>Peak<br />Pos</div>
                    <div className={styles.thinnest}>Wks on<br />Chart</div>
                </div>
                {_data.map((d: ISongData) => {

                    const _award = _highest === d.pos ? <span className={styles.award + " " + styles.topentry}>{_topEntry}</span> :
                        ((d.isNew === true || (d.lastWeek && d.lastWeek > d.pos)) ? <span className={styles.award}>{_awardIcon}</span> :
                            undefined);
                    
                            const _icon = getIcon(d);
                            
                    return <><div onClick={() => { setActiveRow(activeRow===d.pos ? undefined:d.pos) }} key={"week"+d.pos} className={(highlightSong === d.pos ? styles.highlightRow + " " : "") + styles.row
                        + (activeRow === d.pos ? " " + styles.noBorder : "") + (d.pos === _data.length ? " " + styles.lastRow : "")}><div className={styles.thin}>{d.pos}</div><div><img width="100" height="100"
                            src={d.img} /></div><div>{_icon}</div><div><h3>{d.name}</h3><span className={styles.artist}>{d.artist}</span>
                            <span className={styles.expandIcon}>{activeRow === d.pos ? "-":"+"}</span>
                        </div><div className={styles.thinnest}>{_award}</div><div className={styles.thinnest}>
                            {d.lastWeek ? d.lastWeek : "-"}</div><div className={styles.thinnest}>{d.max}</div><div className={styles.thinnest}>{d.weeksInChart}</div></div>
                        {activeRow === d.pos && <div className={styles.panel} onClick={() => { setActiveRow(undefined); }}><Panel
                            currentWeek={_week.week} weeks={weeks} songs={songs} currentSong={d.artist + " - " + d.name} /></div>}
                    </>
                })}
                <DropOuts prevWeek={_prevWeek} thisWeek={_week} songs={songs} />
            </div></>;

    }
    else {
        return <></>;
    }
}

interface IDropOutsProps
{
    prevWeek: IWeek; thisWeek: IWeek; songs: ISong[];
}

function DropOuts(props:IDropOutsProps)
{
    const _dropOuts: ISong[] = [];

    const { prevWeek, thisWeek, songs }  = props;

    if (prevWeek) {
        prevWeek.songs.forEach((s: string) => {
            if (thisWeek.songs.filter(c => c === s).length === 0) {
                _dropOuts.push(getSong(songs, s));
            }
        });
    }

    if(_dropOuts.length>0)
    {
        return  <><h2>This Week's Dropouts</h2>
        <div className={styles.dropOuts}>
            {_dropOuts.map((d) => <div key={d.name + d.artist}><img width="100" height="100" src={d.img} /><div><h2>{d.name}</h2><p>{d.artist}</p></div></div>)}
        </div></>
    }
    else{
        return <></>
    }

}


export function getSongData(week:number, weeks:IWeek[],  songs: ISong[])
{
    const _data: ISongData[] = [];
    let _i: number = 1; let _highest: number | undefined = undefined;

    const _week = weeks.filter(w => w.week === week)[0];
    var prevWeekSongs: string[] = weeks.filter(_w => _w.week === week - 1).length === 1 ? weeks.filter(_w => _w.week === week - 1)[0].songs : [];

    _week.songs.forEach((s: string) => {
        const _lastWeekData: string[] | undefined = prevWeekSongs.filter(p => p === s);
        const _isNew = _lastWeekData.length === 0 ? true : false;
        if (_isNew === true && _highest === undefined) { _highest = _i; }
        const _lastWeek = _isNew === true ? undefined : prevWeekSongs.indexOf(_lastWeekData[0]) + 1;
        let _otherWeeks: IWeek[] = weeks.filter(w => w.week <= week && w.songs.filter(so => so == s).length > 0);
        let _weeksAtOne: number = 0;
        const _weeksInChart: number = _otherWeeks.length;
        let _max: number | undefined = undefined;
        _otherWeeks.forEach((os) => {
            var _pos = os.songs.indexOf(os.songs.filter(z => z === s)[0]);
            if (_pos == 0) { _weeksAtOne++; }
            if ((_max === undefined || _pos < _max) && _pos >= 0) { _max = _pos; }
        });
        _max = _max === undefined ? 1 : _max + 1;
        const _songData = getSong(songs, s);
        _data.push({ pos: _i, name: _songData.name, artist: _songData.artist, img: _songData.img, isNew: _isNew, lastWeek: _lastWeek, weeksAtOne: _weeksAtOne, weeksInChart: _weeksInChart, max: _max })
        _i++;
    });

    return _data;

}

export function getIcon(d:ISongData)
{

    const _same = <svg xmlns="http://www.w3.org/2000/svg" style={{ width: "100%", height: "auto" }} viewBox="0 0 26.191 26.191"><g data-name="Group 3" transform="translate(-626 -1915)"><circle data-name="Ellipse 494" cx="13.095" cy="13.095" r="13.095" transform="translate(626 1915)" fill="#8289a1"></circle><path d="M642.771 1928.989h-10.77v-1.82h10.77l-4.746-4.882 1.251-1.287 6.882 7.079-6.882 7.079-1.251-1.288z" fill="#fff"></path></g></svg>;
    const _faller = <svg xmlns="http://www.w3.org/2000/svg" style={{ width: "100%", height: "auto" }} viewBox="0 0 26.191 26.191"><g data-name="Group 7170" transform="translate(-626 -1965.469)"><circle data-name="Ellipse 494" cx="13.095" cy="13.095" r="13.095" transform="translate(626 1965.469)" fill="#448118"></circle><path d="M639.989 1975.227v10.77h-1.82v-10.77l-4.882 4.746-1.287-1.251 7.079-6.882 7.079 6.882-1.288 1.251z" fill="#fff"></path></g></svg>;
    const _riser = <svg xmlns="http://www.w3.org/2000/svg" style={{ width: "100%", height: "auto" }} viewBox="0 0 26.191 26.191"><g data-name="Group 7171"><g data-name="final red"><circle data-name="Ellipse 494" cx="13.095" cy="13.095" r="13.095" fill="#b91b20"></circle></g><path d="M12.167 17.141V6.371h1.82v10.77l4.882-4.746 1.287 1.251-7.079 6.882-7.079-6.882 1.288-1.251z" fill="#fff"></path></g></svg>;

    let _icon = d.isNew === true ? <span className={styles.new}>NEW</span> : (d.lastWeek as number < d.pos ? <span className={styles.riser}>{_riser}</span> :
        (d.lastWeek as number > d.pos ? <span className={styles.loser}>{_faller}</span> : <span className={styles.same}>{_same}</span>));
    if (d.lastWeek === undefined && d.weeksInChart > 1) {
        _icon = <span className={styles.new + " " + styles.rentry}>RE-<br />ENTRY</span>;
    }
    return _icon

}

export default Week;