import React from 'react';
import styles from './charts.module.scss';
import { customLightTheme, getSong, IChartFile, ISong, ISongData, IWeek } from './App';
import { Link, useParams } from 'react-router-dom';
import { isConstructorDeclaration } from 'typescript';
import { BrandVariants, Button, createLightTheme, FluentProvider, Theme, Tooltip, webLightTheme } from "@fluentui/react-components";

interface IPanelProps {
    weeks: IWeek[]; songs: ISong[]; currentSong: string; currentWeek: number;
}

interface IConsecutive{
    weeks: IConsecutiveWeek[];
}

interface IConsecutiveWeek { 
    position: number; week: string; weekNo: number;
}

export function Panel(props: IPanelProps) {

    const { seg, week} = useParams();

    
    const { weeks, songs, currentSong, currentWeek } = props;
    const _finalWeek = weeks[weeks.length-1].week;
    const song = getSong(songs, currentSong);
    if (song) {

        const chartObj = weeks.filter(w => w.songs.filter(s => s === currentSong).length > 0);
        const firstChartDate: string = chartObj.length > 0 ? chartObj[0].date : "";
        let peakPos: number | undefined = chartObj.length === 0 ? undefined : Math.min(...chartObj.map((w) => { return (w.songs.indexOf(currentSong) as number) }));
        
        const _consecutive:IConsecutive[] = [];
        if(chartObj.length>0)
        {
            let _currentRun:IConsecutiveWeek[] = [];
            weeks.filter(w => w.week>=chartObj[0].week && w.week<=chartObj[chartObj.length-1].week).forEach((w) => {
                if(w.songs.indexOf(currentSong)===-1 && _currentRun.length>0)
                {
                    _consecutive.push({ weeks: _currentRun}); _currentRun = [];
                }
                else if(w.songs.indexOf(currentSong)>=0) {
                    _currentRun.push({ position:  w.songs.indexOf(currentSong) + 1, week: w.date, weekNo: w.week });
                }
                
            });   
            if(_currentRun.length>0) { _consecutive.push({weeks: _currentRun})} 
        }


        return firstChartDate !== "" ? <><h2>Chart data</h2>
            <div className={styles.chartData}>
                <div className={styles.data}><div>Title</div><div>{song.name}</div></div>
                <div className={styles.data}><div>Artist</div><div>{song.artist}</div></div>
            { song.album && <div className={styles.data}><div>Album</div><div>{song.album}</div></div>}
            <div className={styles.data}><div>Peak position</div><div>{(peakPos as number) + 1}</div></div>
            <div className={styles.data}><div>First chart date</div><div>{firstChartDate}</div></div>
            <div className={styles.data}><div>Weeks on chart</div><div>{chartObj.length}</div></div></div>
            <div className={styles.chartRunHeader}><h2>Chart run</h2><div className={styles.key}><div>Selected week</div><div>Most recent week</div><div>Peak position</div></div></div>
            {_consecutive.map((h, index) => <div key={index}><h3>{h.weeks[0].week} {h.weeks.length>1 && <> - {h.weeks[h.weeks.length-1].week}</>}</h3>
            <FluentProvider theme={customLightTheme()}><div className={styles.history}>{h.weeks.map(w => 
                 <Tooltip                                  
                 withArrow
                 content={w.weekNo===currentWeek ? w.week + " - this is the week you are viewing":(w.weekNo === _finalWeek ? w.week + " - the most recent week of the chart":(((peakPos as number)+1)===w.position ? w.week + ", peak position": w.week))}
                 relationship="label"
               ><Link key={w.weekNo} to={"/chart/" + seg + "/week/" + w.weekNo + "/song/" + w.position} 
                onClick={(event) => { if(w.weekNo === currentWeek) { event.preventDefault(); event.stopPropagation();}}}
                className={(w.position === ((peakPos as number)+1) ? styles.isPeak:"") + (w.weekNo === currentWeek ? " " + styles.noClick + " " + styles.currentSelectedWeek:"") + (_finalWeek===w.weekNo ? " " + styles.currentWeek:"")}>{w.position}</Link></Tooltip>)}</div></FluentProvider></div>)}
                {song.spotify && <><h2>Listen here</h2><iframe style={{borderRadius:"12px"}} src={song.spotify} width="100%" height="152" frameBorder="0" allowFullScreen={false} allow="autoplay; clipboard-write; encrypted-media; fullscreen; picture-in-picture" loading="lazy"></iframe></>}
            </> : <></>
    }
    else {
        return <></>
    }
}