import React from 'react';
import styles from './charts.module.scss';
import { IChart, IChartFile, IWeek, Navigation } from './App';
import { useParams } from 'react-router-dom';
import Week from './week';

export interface IChartProps
{
  charts:IChartFile; setSeg: (a:string) => void;
}

export function Chart(props:any) {

  const { seg, week} = useParams();
  const { charts, setSeg } = props;
  const [ song, setSong ] = React.useState<string|undefined>(undefined);

  React.useEffect(() => {
    setSeg(seg);
  }, []);

  if(charts && seg)
  {
    const chart = charts.filter((c:IChart) => c.urlseg===seg)[0];
    if(chart && chart.data)
    {
      window.document.title = chart.name;
      const weeks = chart.data.weeks;
      if(chart.data && chart.data.songs && chart.data.songs.length>0)
      {
        let _week = chart.data.weeks[chart.data.weeks.length-1].week; 
        if(week && chart.data.weeks.filter((w:IWeek) => w.week.toString()===week).length===1)
        {
          _week = parseInt(week);
        }             
        return <><Week startDay={chart.startDay} rating={chart.rating} key={seg + "-" + _week} name={chart.name} seg={seg} songs={chart.data.songs} week={_week} weeks={chart.data.weeks}/></>
      }
    }
  }
  return null;
}

